(function() {
  this.App.service('MnoErrorsHandler', ["$log", function($log) {
    var _self, errorCache;
    _self = this;
    errorCache = null;
    this.processServerError = function(serverError, form) {
      var ref;
      $log.error('An error occurred:', serverError);
      if (!form) {
        return;
      }
      if (_.startsWith(serverError.data, '<!DOCTYPE html>')) {
        return;
      }
      if ((400 <= (ref = serverError.status) && ref <= 499)) {
        errorCache = serverError;
        return _.each(errorCache.data, function(errors, key) {
          return _.each(errors, function() {
            if (form[key] != null) {
              return form[key].$setValidity('server', false);
            } else {
              return $log.error('MnoErrorsHandler: cannot find field:' + key);
            }
          });
        });
      }
    };
    this.errorMessage = function(name) {
      var result;
      result = [];
      if (errorCache != null) {
        _.each(errorCache.data[name], function(msg) {
          return result.push(msg);
        });
      }
      return result.join(', ');
    };
    this.resetErrors = function(form) {
      if (errorCache != null) {
        _.each(errorCache.data, function(errors, key) {
          return _.each(errors, function() {
            if (form[key] != null) {
              return form[key].$setValidity('server', null);
            }
          });
        });
        return errorCache = null;
      }
    };
    this.onlyServerError = function(form) {
      return _.every(form.$error, function(v, k) {
        return k === 'server';
      });
    };
    return this;
  }]);

}).call(this);

(function() {
  this.App.component('mnoFileUploader', {
    bindings: {
      maxSize: '@',
      uploadUri: '@',
      fileTypes: '@',
      startCallback: '&',
      successCallback: '&',
      errorCallback: '&'
    },
    template: '<form name="$ctrl.form" novalidate>\n  <fieldset>\n    <input type="file" accept="{{$ctrl.fileTypes}}" ngf-select ng-model="$ctrl.file" name="file" ngf-max-size="{{$ctrl.maxSize}}" required ngf-model-invalid="errorFile">\n    <i ng-show="$ctrl.form.file.$error.maxSize">\n      <span translate>mnoe_admin_panel.components.mno-file-uploader.file_too_large</span> : {{errorFile.size / 1000000|number:1}}MB, max {{$ctrl.maxSize}}\n    </i>\n    <div class="top-buffer-1">\n      <button type="button" class="btn btn-primary" ng-disabled="!$ctrl.form.$valid || $ctrl.isUploading" ng-click="$ctrl.uploadFile()" translate>\n        mnoe_admin_panel.components.mno-file-uploader.submit\n      </button>\n      <span ng-show="$ctrl.isUploading"><i class="fa fa-spinner fa-pulse fa-fw"></i>&nbsp;</span>\n    </div>\n  </fieldset>\n  <div class="progress top-buffer-1" ng-show="$ctrl.file.progress >= 0">\n    <uib-progressbar value="$ctrl.file.progress" type="{{$ctrl.progressBarType}}">\n      <span ng-show="$ctrl.errors" translate>mnoe_admin_panel.components.mno-file-uploader.upload_failed</span>\n      <span ng-show="$ctrl.file.result" translate>mnoe_admin_panel.components.mno-file-uploader.upload_successful</span>\n      <span ng-show="!$ctrl.file.result && !$ctrl.errors">{{$ctrl.file.progress}}%</span>\n    </uib-progressbar>\n  </div>\n</form>',
    controller: ["$timeout", "Upload", "MnoErrorsHandler", function($timeout, Upload, MnoErrorsHandler) {
      var ctrl;
      ctrl = this;
      ctrl.uploadFile = function() {
        ctrl.startCallback();
        ctrl.progressBarType = 'primary';
        ctrl.isUploading = true;
        ctrl.hasErrors = false;
        ctrl.file.upload = Upload.upload({
          headers: {
            'Accept': 'application/json'
          },
          url: ctrl.uploadUri,
          data: {
            file: ctrl.file
          }
        });
        return ctrl.file.upload.then(function(result) {
          $timeout(function() {
            ctrl.file.result = true;
            return ctrl.form.$setPristine();
          });
          $timeout((function() {
            return ctrl.file.progress = -1;
          }), 3000);
          return ctrl.successCallback({
            value: result
          });
        }, function(error) {
          MnoErrorsHandler.processServerError(error);
          ctrl.hasErrors = true;
          ctrl.errorCallback({
            value: error
          });
          return ctrl.progressBarType = 'danger';
        }, function(evt) {
          return ctrl.file.progress = parseInt(100.0 * evt.loaded / evt.total);
        })["finally"](function() {
          return ctrl.isUploading = false;
        });
      };
    }]
  });

}).call(this);

(function() {
  this.App.component('mnoImageManager', {
    bindings: {
      assets: '<',
      uploadUri: '<',
      fieldName: '<',
      deleteCb: '=',
      isDisabled: '<'
    },
    template: '<form name="vm.imageForm" class="form" novalidate>\n  <mno-image-selector ng-model="$ctrl.image"\n                      is-disabled="$ctrl.isDisabled"\n                      max-size="\'5MB\'">\n  </mno-image-selector>\n</form>\n\n<button class="btn btn-primary top-buffer-1"\n        ng-if="$ctrl.image"\n        ng-disabled="!$ctrl.image || $ctrl.isDisabled"\n        ng-click="$ctrl.uploadImage($ctrl.image)" translate>\n  Submit\n</button>\n\n<!-- TODO: implement sortable -->\n<ul ui-sortable="$ctrl.sortableOptions" ng-model="$ctrl.assets" class="assets-list top-buffer-1 not-sortable">\n  <li class="asset" ng-repeat="asset in $ctrl.assets" ng-class="{\'not-sortable\': $ctrl.isDisabled}">\n    <span class="asset-close" ng-click="$ctrl.deleteImage(asset)" ng-show="!$ctrl.isDisabled">\n      <i class="fa fa-close"></i>\n    </span>\n    <img ng-src="{{asset.url}}" class="img-thumbnail">\n  </li>\n</ul>',
    controller: ["$timeout", "orderByFilter", "Upload", "MnoErrorsHandler", function($timeout, orderByFilter, Upload, MnoErrorsHandler) {
      var ctrl;
      ctrl = this;
      ctrl.$onInit = function() {
        return ctrl.assets = orderByFilter(ctrl.assets, 'position');
      };
      ctrl.sortableOptions = {
        cancel: ".not-sortable",
        stop: function(e, ui) {
          var asset, index, results;
          results = [];
          for (index in ctrl.assets) {
            asset = ctrl.assets[index];
            index = parseInt(index);
            if (asset.attributes.position !== index) {
              asset.attributes.position = index;
              results.push(asset.patch({
                position: index
              }));
            } else {
              results.push(void 0);
            }
          }
          return results;
        }
      };
      ctrl.uploadImage = function() {
        var uploadPromise;
        uploadPromise = Upload.upload({
          url: ctrl.uploadUri,
          data: {
            field_name: ctrl.fieldName,
            content: ctrl.image
          }
        });
        return uploadPromise.then(function(response) {
          console.log("### DEBUG response", response);
          ctrl.assets.push(response.data);
          return $timeout(function() {
            return ctrl.image.result = true;
          });
        }, function(error) {
          MnoErrorsHandler.processServerError(error);
          if (error.status > 0) {
            return ctrl.image.error = true;
          }
        }, function(evt) {
          return ctrl.image.progress = parseInt(100.0 * evt.loaded / evt.total);
        })["finally"](function() {
          return $timeout(function() {
            return ctrl.image = null;
          }, 1000);
        });
      };
      ctrl.deleteImage = function(image) {
        return ctrl.deleteCb(image).then(function() {
          return _.remove(ctrl.assets, image);
        });
      };
    }]
  });

}).call(this);

