(function() {
  this.App.component('mnoProductSelectorModal', {
    bindings: {
      resolve: '<',
      close: '&',
      dismiss: '&'
    },
    templateUrl: 'app/components/mno-product-selector/mno-product-selector.html',
    controller: ["$window", "orderByFilter", function($window, orderByFilter) {
      'ngInject';
      var $ctrl;
      $ctrl = this;
      $ctrl.$onInit = function() {
        $ctrl.products = orderByFilter($ctrl.resolve.products.data, 'name');
        $ctrl.multiple = $ctrl.resolve.multiple;
        $ctrl.modalHeight = ($window.innerHeight - 200) + "px";
        $ctrl.selectedProducts = [];
        $ctrl.headerText = $ctrl.resolve.headerText || 'mnoe_admin_panel.components.mno-product-selector.title';
        return $ctrl.actionButtonText = $ctrl.resolve.actionButtonText || 'mnoe_admin_panel.components.mno-product-selector.create_order';
      };
      $ctrl.toggleProduct = function(product) {
        if (product.checked) {
          _.remove($ctrl.selectedProducts, product);
        } else {
          if (!$ctrl.multiple && $ctrl.selectedProducts.length > 0) {
            return;
          }
          $ctrl.selectedProducts.push(product);
        }
        return product.checked = !product.checked;
      };
      $ctrl.closeModal = function() {
        return $ctrl.close({
          $value: $ctrl.multiple ? $ctrl.selectedProducts : $ctrl.selectedProducts[0]
        });
      };
      $ctrl.dismissModal = function() {
        return $ctrl.dismiss();
      };
    }]
  });

}).call(this);

(function() {
  this.App.factory('MnoeApiSvc', ["$log", "Restangular", "inflector", function($log, Restangular, inflector) {
    return Restangular.withConfig(function(RestangularProvider) {
      RestangularProvider.setBaseUrl('/mnoe/jpi/v1');
      RestangularProvider.setDefaultHeaders({
        Accept: "application/json"
      });
      RestangularProvider.setFullResponse(true);
      return RestangularProvider.addResponseInterceptor(function(data, operation, what, url, response, deferred) {
        var extractedData;
        if (_.startsWith(what, '/')) {
          return data;
        }
        extractedData = null;
        if (operation === 'getList') {
          extractedData = data[what];
        } else if (operation === 'get' || operation === 'put' || operation === 'post') {
          what = inflector.singularize(what);
          extractedData = data[what];
        } else {
          extractedData = data;
        }
        return extractedData;
      });
    });
  }]);

}).call(this);

