(function() {
  this.App.service('DatesHelper', function() {
    var _self;
    _self = this;
    this.expectedPaymentDate = function(endOfInvoiceDate) {
      var endOfInvoicePeriod, secondOftheEndOfInvoicePeriod;
      endOfInvoicePeriod = moment(endOfInvoiceDate, 'YYYY-M-D');
      secondOftheEndOfInvoicePeriod = moment(endOfInvoicePeriod, 'YYYY-M-D').startOf('month').add(1, 'day');
      if (endOfInvoicePeriod < secondOftheEndOfInvoicePeriod) {
        return secondOftheEndOfInvoicePeriod;
      } else {
        return moment(endOfInvoicePeriod, 'YYYY-M-D').startOf('month').add(1, 'month').add(1, 'day');
      }
    };
  });

}).call(this);

