(function() {
  this.App.controller('CreateMarkupController', ["$scope", "$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeProductMarkups", "MnoeProducts", "MnoeOrganizations", "MnoErrorsHandler", function($scope, $filter, $stateParams, $log, $uibModalInstance, toastr, MnoeProductMarkups, MnoeProducts, MnoeOrganizations, MnoErrorsHandler) {
    'ngInject';
    var vm;
    vm = this;
    vm.nbItems = 10;
    vm.debounce_time = 100;
    vm.markup = {};
    vm.products = [];
    vm.companies = [];
    vm.markup.product_id = null;
    vm.markup.organization_id = null;
    vm.onSubmit = function() {
      vm.isLoading = true;
      return vm.productMarkupExist().then(function(exists) {
        if (!exists) {
          return MnoeProductMarkups.addProductMarkup(vm.markup).then(function(success) {
            toastr.success("mnoe_admin_panel.dashboard.product_markups.add_markup.modal.toastr_success", {
              preventDuplicates: false
            });
            return $uibModalInstance.close(success.data);
          }, function(error) {
            toastr.error('mnoe_admin_panel.dashboard.product_markups.add_markup.modal.toastr_error', {
              preventDuplicates: false
            });
            return $log.error("An error occurred:", error);
          })["finally"](function() {
            return vm.isLoading = false;
          });
        } else {
          $uibModalInstance.close();
          return toastr.error('mnoe_admin_panel.dashboard.product_markups.add_markup.modal.already_exists', {
            preventDuplicates: false
          });
        }
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.searchProducts = function(search, timeoutPromise) {
      return MnoeProducts.products(vm.nbItems, 0, 'name', {
        'where[name.like]': search + '%'
      });
    };
    vm.searchCompanies = function(search, timeoutPromise) {
      return MnoeOrganizations.organizations(vm.nbItems, 0, 'name', {
        'where[name.like]': search + '%'
      });
    };
    vm.toggleProductFilter = function(product) {
      return vm.markup.product_id = product.originalObject.id;
    };
    vm.toggleCompanyFilter = function(company) {
      return vm.markup.organization_id = company.originalObject.id;
    };
    vm.productMarkupExist = function() {
      return MnoeProductMarkups.search(_.pick({
        'product.id': vm.markup.product_id,
        'organization.id': vm.markup.organization_id
      }, _.identity)).then(function(success) {
        var exists;
        exists = false;
        if (success.data.length > 0) {
          if ((vm.markup.product_id != null) && (vm.markup.organization_id != null)) {
            return true;
          }
          _.each(success.data, function(pm) {
            if ((vm.markup.product_id == null) && !pm.product && pm.organization.id === vm.markup.organization_id) {
              exists = true;
            }
            if ((vm.markup.organization_id == null) && !pm.organization && pm.product.id === vm.markup.product_id) {
              exists = true;
            }
            if ((vm.markup.product_id == null) && (vm.markup.organization_id == null) && !pm.product && !pm.organization) {
              return exists = true;
            }
          });
        }
        return exists;
      }, function(error) {
        return false;
      });
    };
  }]);

}).call(this);

